.header{
    text-align: center;
}
.header img{
    width: 290px;
    margin-bottom: 15px;
}

.header nav{
    background-color: black;
    padding: 10px;
    text-align: left;
}

.header nav a{
    text-decoration: none;
    color: white;
    padding: 10px;
    font-size: 18px;
}

.header nav a:hover{
    color: rebeccapurple;
    background-color: thistle;
    padding: 10px;
}